<template>
  <section class="dashboard-content">
    <div class="form-section">
      <div class="section-title mb-s">问题件登记</div>
      <div class="flex">
        <div class="flex-3">
          <div class="mt-m">
            <span class="label" type="text">导入问题件表</span>
            <el-button size="small" @click="$refs.upload.click()">选择文件</el-button>
            <span class="remark" v-if="file">{{ file.name }}</span>
          </div>
          <div class="mt-m">
            <el-button size="small" type="primary" @click="uploadFile" :disabled="!file" :loading="!isLoaded">确认导入</el-button>
            <span class="remark">重复的记录以新上传的为准</span>
          </div>
          <input type="file" ref="upload" @change="chooseFile">
        </div>
        <div class="flex-3 border-l pl-l">
          <label class="filter-label">
            <span class="label-content">单个快递单号</span>
            <el-input v-model="orderId"></el-input>
          </label>
          <div>
            <el-button size="small" type="primary" :disabled="!orderId">确认添加</el-button>
          </div>
        </div>
      </div>
    </div>
    <div class="form-section">
      <div class="section-title">全部问题件</div>
      <div class="mt-s">
        <span class="label" type="text">生成未处理完的问题件表</span>
      </div>
      <div class="mt-m">
        <el-button size="small" type="primary" v-if="true">确认生成</el-button>
        <el-button size="small" :loading="true" v-else>生成中</el-button>
        <span class="remark">未处理完的问题件表主要用于向快递客服反馈</span>
      </div>
    </div>
    <el-table stripe :data="troubles">
      <el-table-column prop="targetId" label="订单ID" />
      <el-table-column label="跟进状态">
        <template slot-scope="scope">
          {{ scope.row.isActive ? '跟进中' : '已结束' }}
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      class="pagination"
      @size-change="changeSize"
      @current-change="changePage"
      :current-page="pagination.page"
      :page-sizes="[20, 50, 100]"
      :page-size="pagination.limit"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pagination.total">
    </el-pagination>
  </section>
</template>

<script>
export default {
  name: 'Trouble',
  data() {
    return {
      orderId: '',
      isLoaded: true,
      file: null,
      troubles: [],
      pagination: {
        page: 1,
        limit: 20,
        total: 0,
      },
    };
  },
  mounted() {
    this.loadData();
  },
  methods: {
    chooseFile(e) {
      this.file = e.target.files ? e.target.files[0] : null;
    },
    uploadFile() {
    },
    changeSize(limit) {
      this.pagination.limit = limit;
      this.loadData();
    },
    changePage(page) {
      this.pagination.page = page;
      this.loadData();
    },
    loadData() {
      const { limit, page } = this.pagination;
      this.isLoaded = false;
      this.$http.get('/api/troubles', {
        params: { limit, page },
      }).then(({ data }) => {
        this.isLoaded = true;
        this.pagination.total = data.total;
        this.troubles = data.troubles;
      });
    },
  },
};
</script>
